import React from 'react';
import styled from 'styled-components';

const ResultValue = styled.div`
  max-width: 400px;
  height: 100px;
  background-color: #aaa;
`;

const ExampleResult = (search, setSearch) => {
  // console.log(data, 'data 데이터 입력이 된거야?');
  // const [infos, setInfos] = useState([]);

  // const onClick = async () => {
  //   try {
  //     const res = await h();
  //     console.log(res, 'res에 뭐있나 봅시다.');
  //   } catch (error) {
  //     console.log(error, '데이터 가져오기 오류');
  //   }
  // };

  return (
    <ResultValue>
      {/* <form onSubmit={e => e.preventDefault()} method='GET'> */}
      <input
        type='text'
        value={search}
        onChange={e => setSearch(e.target.value)}
        autoComplete='off'
        placeholder='찾을 데이터 값을 입력하세요.'
      />
      {/* <button onClick={onClick}>찾기</button> */}
      {/* </form> */}
    </ResultValue>
  );
};

export default ExampleResult;
